<template>
  <div class="user-container">
    <!-- 用户基本信息面板 -->
    <div class="user-card">
      <!-- 用户头像、姓名 -->
      <van-cell>
        <!-- 使用 title 插槽来自定义标题 -->
        <template #icon>
          <img :src="$store.state.userPhoto" alt="" class="avatar">
        </template>
        <template #title>
          <span class="username">{{$store.state.userName}}</span>
        </template>
        <template #label>
          <van-tag color="#fff" text-color="#007bff">{{userInfo.certi||'申请认证'}}
          </van-tag>
        </template>
      </van-cell>
      <!-- 动态、关注、粉丝 -->
      <div class="user-data">
        <div class="user-data-item">
          <span>{{userInfo.art_count}}</span>
          <span>动态</span>
        </div>
        <div class="user-data-item">
          <span>{{userInfo.follow_count}}</span>
          <span>关注</span>
        </div>
        <div class="user-data-item">
          <span>{{userInfo.fans_count}}</span>
          <span>粉丝</span>
        </div>
      </div>
    </div>

    <!-- 操作面板 -->
    <van-cell-group class="action-card">
      <van-cell icon="edit" title="编辑资料" is-link @click="userEditClickFn" />
      <van-cell icon="chat-o" title="小思同学" is-link
        @click="$router.push('/chat')" />
      <van-cell icon="warning-o" title="退出登录" is-link @click="logOutFn" />
    </van-cell-group>

  </div>
</template>

<script>
import { getUserInfoAPI } from '@/api'
import { removeToken } from '@/utils/token'
import { Dialog } from 'vant'
import { mapMutations } from 'vuex'
export default {
  name: 'MyUser',
  data () {
    return {
      userInfo: {}
    }
  },
  async created () {
    const res = await getUserInfoAPI()
    console.log(res)
    this.userInfo = res.data.data
    // 把头像传给 vuex中的state
    // this.$store.commit('SET_USERPHOTO',
    //   this.userInfo.photo)
    this.SET_USERPHOTO(this.userInfo.photo)
    this.SET_USERNAME(this.userInfo.name)
  },
  methods: {
    // vuex 调用
    ...mapMutations(['SET_USERPHOTO', 'SET_USERNAME']),
    userEditClickFn () {
      this.$router.push('/user_editor')
    },
    // 退出
    logOutFn () {
      Dialog.confirm({
        title: '是否退出登录',
        message: '这就走了？不爱我了吗？'
      })
        .then(() => {
          removeToken()
          this.$router.replace('/login')
        })
        .catch(() => {
          // on cancel
        })
    }
  }
}
</script>

<style scoped lang="less">
.user-container {
  .user-card {
    background-color: #007bff;
    color: white;
    padding-top: 20px;
    .van-cell {
      background: #007bff;
      color: white;
      &::after {
        display: none;
      }
      .avatar {
        width: 60px;
        height: 60px;
        background-color: #fff;
        border-radius: 50%;
        margin-right: 10px;
      }
      .username {
        font-size: 14px;
        font-weight: bold;
      }
    }
  }
  .user-data {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-size: 14px;
    padding: 30px 0;
    .user-data-item {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 33.33%;
    }
  }
}
</style>
